___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --img-size: 300px;
    --shadow-normal: 5px 5px 10px rgb(0, 0, 0, 0.25);
    --padding-normal: 8px 16px;
}

* {
    font-family: Arial, Helvetica, sans-serif;
}

body {
    margin: 0;
    padding: 0;
    height: 100vh;
    display: grid;
    grid-template: max-content repeat(3, 1fr) max-content / repeat(3, 1fr);
    grid-template-areas:
        "header header header"
        ". . ."
        ". image-carousel-container ."
        ". . ."
        ". footer .";
}

header {
    grid-area: header;
    text-align: center;
    background-color: black;
    color: white;
    text-shadow: 2px 2px 5px #ffffff75;
    box-shadow: var(--shadow-normal);
    padding: var(--padding-normal);
}

.title-link {
    color: white;
}

.title-link:hover {
    text-decoration: none;
    text-shadow: 2px 2px 5px #ffffff50;
    opacity: 50%;
}

.image-carousel-container {
    grid-area: image-carousel-container;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    margin: 0;
    padding: 0;
}

footer {
    grid-area: footer;
    text-align: center;
}

.credit {
    padding: 8px 4px;
    border-radius: 25px;
    color: white;
}

.credit:not(.credit-link) {
    background-color: #00000075;
    box-shadow: var(--shadow-normal);
}

.credit-link:hover {
    text-decoration: none;
    background-color: #00000075;
}
